import React from "react";
import { Link } from "react-router-dom";
import "../../assets/css/navbar.css";
import { UserContext } from "../../App";
import logo from "../../assets/images/IMG_0973-removebg-preview.png";
import { useTranslation } from "react-i18next";
import cookie from "js-cookie";
const Navbar = () => {
  const { state, dispatch } = React.useContext(UserContext);
  const { i18n, t } = useTranslation();
  function handleClick(lang) {
    i18n.changeLanguage(lang);
  }
  React.useEffect(() => {
    const lan = localStorage.getItem("i18nextLng");
    if (lan == "ru") {
      document.getElementById("lang").innerHTML = "RU ";
    }
  }, []);
  const renderList = () => {
    if (state) {
      return (
        <React.Fragment>
          <li>
            <Link to='home'>{t("navbar.1")}</Link>
          </li>
          <li>
            <Link to='about'>{t("navbar.2")}</Link>
          </li>
          <li>
            <Link to='client'>{t("navbar.3")}</Link>
          </li>
          <li>
            <Link to='contact'>{t("navbar.4")}</Link>
          </li>
          <li className='dropdown'>
            <a
              className='dropdown-toggle'
              data-toggle='dropdown'
              role='button'
              aria-haspopup='true'
              aria-expanded='false'>
              {t("navbar.6")}
              <i className='fas fa-chevron-down'></i>
            </a>
            <ul className='dropdown-menu'>
              <li>
                <Link to='employeeDetails'>{t("navbar.7")}</Link>
              </li>
              <li>
                <Link to='updateEmployee'>{t("navbar.8")}</Link>
              </li>
              <li>
                <Link to='addEmployee'>{t("navbar.9")}</Link>
              </li>
            </ul>
          </li>

          <li>
            <a
              href='/'
              onClick={() => {
                cookie.remove("jwt-token");
                dispatch({ type: "CLEAR" });
              }}>
              {t("navbar.10")}
            </a>
          </li>

          <li style={{ top: "8px" }}>
            <div className='btn-group'>
              <button type='button' className='btn btn-default'>
                <span id='lang'>EN </span>
                <i className='fa fa-globe' aria-hidden='true'></i>
              </button>
              <button
                type='button'
                className='btn btn-default dropdown-toggle'
                data-toggle='dropdown'
                aria-haspopup='true'
                aria-expanded='false'>
                <span className='caret'></span>
                <span className='sr-only'></span>
              </button>
              <ul className='dropdown-menu'>
                <li
                  className='btn'
                  onClick={() => {
                    handleClick("en");
                    document.getElementById("lang").innerHTML = "EN ";
                  }}>
                  English
                </li>
                <li
                  className='btn'
                  onClick={() => {
                    handleClick("ru");
                    document.getElementById("lang").innerHTML = "RU ";
                  }}>
                  Russian
                </li>
              </ul>
            </div>
          </li>
        </React.Fragment>
      );
    } else {
      return (
        <React.Fragment>
          <li>
            <Link to='home'>{t("navbar.1")}</Link>
          </li>
          <li>
            <Link to='about'>{t("navbar.2")}</Link>
          </li>
          <li>
            <Link to='client'>{t("navbar.3")}</Link>
          </li>
          <li>
            <Link to='contact'>{t("navbar.4")}</Link>
          </li>
          <li>
            <Link to='employee'>{t("navbar.11")}</Link>
          </li>

          <li style={{ top: "8px" }}>
            <div className='btn-group'>
              <button type='button' className='btn btn-default'>
                <span id='lang'>EN </span>
                <i className='fa fa-globe' aria-hidden='true'></i>
              </button>
              <button
                type='button'
                className='btn btn-default dropdown-toggle'
                data-toggle='dropdown'
                aria-haspopup='true'
                aria-expanded='false'>
                <span className='caret'></span>
                <span className='sr-only'></span>
              </button>
              <ul className='dropdown-menu'>
                <li
                  className='btn'
                  onClick={() => {
                    handleClick("en");
                    document.getElementById("lang").innerHTML = "EN ";
                  }}>
                  English
                </li>
                <li
                  className='btn'
                  onClick={() => {
                    handleClick("ru");
                    document.getElementById("lang").innerHTML = "RU ";
                  }}>
                  Russian
                </li>
              </ul>
            </div>
          </li>
        </React.Fragment>
      );
    }
  };
  return (
    <React.Fragment>
      <section className='header width box-shadow'>
        <div className='container-fluid'>
          <div className='row'>
            <div className='width'>
              <div className='navbar-header'>
                <button
                  type='button'
                  className='navbar-toggle collapsed'
                  data-toggle='collapse'
                  data-target='#bs-example-navbar-collapse-1'
                  aria-expanded='false'>
                  <span className='sr-only'>Toggle navigation</span>
                  <span className='icon-bar'></span>
                  <span className='icon-bar'></span>
                  <span className='icon-bar'></span>
                </button>
                <Link className='navbar-brand' to='/'>
                  <img src={logo} alt='logo' />
                  <h4>
                    <span>{t("navbar.12")}</span>
                    {t("navbar.13")}
                  </h4>
                </Link>
              </div>
              <div className='collapse navbar-collapse' id='bs-example-navbar-collapse-1'>
                <ul className='nav navbar-nav navigation-menu'>{renderList()}</ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </React.Fragment>
  );
};

export default Navbar;
