import React, { useContext, createContext, useReducer, useEffect } from "react";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import "./App.css";
import { BrowserRouter, Route, Switch, useHistory } from "react-router-dom";
import Table from "./Components/adminComponent/Table";
import Login from "./Components/Screens/Login";
import AddEmployee from "./Components/adminComponent/AddEmployee";
import UpdateEmployee from "./Components/adminComponent/UpdateEmployee";
import Navbar from "./Components/Screens/Navbar";
import Footer from "./Components/Screens/Footer";
import Home from "./Components/Screens/Home";
import About from "./Components/Screens/About";
import Client from "./Components/Screens/Client";
import { reducer, initialState } from "./reducer/reducer";
import cookie from "js-cookie";
import { ContactUs } from "./Components/Screens/ContactUs";
import Services from "./Components/Screens/Services";
import Serviceconc from "./Components/Screens/Serviceconc";
import Servicecivil from "./Components/Screens/Servicecivil";
export const UserContext = createContext();
const Routing = () => {
  const history = useHistory();
  const { dispatch } = useContext(UserContext);
  useEffect(() => {
    const token = cookie.get("jwt-token");
    if (token) {
      dispatch({ type: "USER", payload: token });
    } else {
      history.push("/");
    }
  }, [history, dispatch]);
  return (
    <Switch>
      <Route exact path='/'>
        <Home />
      </Route>
      <Route path='/home'>
        <Home />
      </Route>
      <Route path='/about'>
        <About />
      </Route>
      <Route path='/client'>
        <Client />
      </Route>
      <Route path='/contact'>
        <ContactUs />
      </Route>
      <Route path='/employee'>
        <Login />
      </Route>
      <Route path='/services'>
        <Services />
      </Route>
      <Route path='/consulting'>
        <Serviceconc />
      </Route>
      <Route path='/civilwork'>
        <Servicecivil />
      </Route>
      <Route path='/employeeDetails'>
        <Table />
      </Route>
      <Route path='/addEmployee'>
        <AddEmployee />
      </Route>

      <Route path='/updateEmployee'>
        <UpdateEmployee />
      </Route>
    </Switch>
  );
};

function App() {
  const [state, dispatch] = useReducer(reducer, initialState);
  return (
    <UserContext.Provider value={{ state, dispatch }}>
      <BrowserRouter>
        <Navbar />
        <Routing />
        <Footer />
      </BrowserRouter>
    </UserContext.Provider>
  );
}

export default App;
