import React from "react";
import cookie from "js-cookie";
import img3 from "../../assets/images/img3.jpg";
import "../../assets/css/login.css";
import laoding from "../../assets/images/loading.svg";
import { toast, Zoom } from "react-toastify";
toast.configure();
const AddEmployee = () => {
  const url = "https://dev-steel-solution.herokuapp.com/api";
  const [empadd, setEmpadd] = React.useState("");
  const [name, setName] = React.useState("");
  const [epf, setEpf] = React.useState("");
  const [esi, setEsi] = React.useState("");
  const [salary, setSalary] = React.useState("");
  const [advanceAmount, setAdvanceAmount] = React.useState("");
  const [dueAmount, setDueAmount] = React.useState("");
  const [attandance, setAttandance] = React.useState("");
  const [id, setId] = React.useState("");
  const [track, setTrack] = React.useState(0);

  const toggleForm = () => {
    let Conatainer = document.querySelector(".Container");
    Conatainer.classList.toggle("active");
  };
  React.useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, []);
  const handleDelete = () => {
    if (!id) {
      toast.error("Field cannot be empty", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        transition: Zoom,
      });
    } else {
      setTrack(1);
      let myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      let requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };
      fetch(url + `/individual-employee/${id}`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (!result) {
            setTrack(0);
            toast.error("Incorrect Employee Id", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: true,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              transition: Zoom,
            });
          } else {
            var myHeaders = new Headers();
            myHeaders.append("Authorization", "Bearer " + cookie.get("jwt-token"));
            myHeaders.append("Content-Type", "application/json");

            let requestOptions = {
              method: "DELETE",
              headers: myHeaders,

              redirect: "follow",
            };

            fetch(url + `/${result._id}`, requestOptions)
              .then((response) => response.json())
              .then((result) => {
                console.log(result);
                if (result.message === "selected employee data deleted successfully") {
                  setTrack(0);
                  setId("");
                  toast.info("Employee deleted", {
                    position: "top-right",
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    transition: Zoom,
                  });
                } else {
                  toast.error("Something went wrong", {
                    position: "top-right",
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    transition: Zoom,
                  });
                  setTrack(0);
                }
              })
              .catch((error) => console.log("error", error));
          }
        })
        .catch((error) => console.log("error", error));
    }
  };

  const handleAdd = () => {
    if (!name || !epf || !esi || !salary || !advanceAmount || !attandance || !dueAmount) {
      toast.error("All Filed must be field", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        transition: Zoom,
      });
    } else {
      setTrack(1);
      var myHeaders = new Headers();
      myHeaders.append("Authorization", "Bearer " + cookie.get("jwt-token"));
      myHeaders.append("Content-Type", "application/json");

      var raw = JSON.stringify({
        name: name,
        epf: epf,
        esi: esi,
        salary: salary,
        advanceAmount: advanceAmount,
        attandance: attandance,
        dueAmount: dueAmount,
      });

      let requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw,
        redirect: "follow",
      };

      fetch(url + `/add-employee`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          setTrack(0);
          toast.info("New Employee Added", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            transition: Zoom,
          });
          setAdvanceAmount("");
          setAttandance("");
          setDueAmount("");
          setEpf("");
          setEsi("");
          setName("");
          setSalary("");
          setEmpadd(result.result);
          console.log(result);
        })
        .catch((error) => console.log("error", error));
    }
  };

  return (
    <React.Fragment>
      <section className='loginForm addform'>
        <div className='Container'>
          <div className='user signinBx'>
            <div className='imgBx'>
              <img src={img3} alt='' />
            </div>
            <div className='formBx delh2'>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  handleDelete();
                }}>
                <h2>Delete Employee</h2>
                <input
                  type='text'
                  placeholder='Enter Employee ID'
                  value={id}
                  onChange={(e) => setId(e.target.value)}
                />
                <input type='submit' />
                <p className='Login'>
                  To Add Employee
                  <a
                    className='btn'
                    onClick={(e) => {
                      e.preventDefault();
                      toggleForm();
                    }}>
                    Click here
                  </a>
                </p>
                {track ? (
                  <img
                    src={laoding}
                    alt='Loading'
                    style={{ width: "65px", height: "65px" }}
                  />
                ) : (
                  ""
                )}
              </form>
            </div>
          </div>
          <div className='user signupBx'>
            <div className='formBx'>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  handleAdd();
                }}>
                <input
                  type='text'
                  placeholder='name'
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
                <input
                  type='text'
                  placeholder='epf'
                  value={epf}
                  onChange={(e) => setEpf(e.target.value)}
                />
                <input
                  type='text'
                  placeholder='esi'
                  value={esi}
                  onChange={(e) => setEsi(e.target.value)}
                />
                <input
                  type='number'
                  placeholder='salary'
                  value={salary}
                  onChange={(e) => setSalary(e.target.value)}
                />
                <input
                  type='number'
                  placeholder='advanceAmount'
                  value={advanceAmount}
                  onChange={(e) => setAdvanceAmount(e.target.value)}
                />
                <input
                  type='number'
                  placeholder='attendance'
                  value={attandance}
                  onChange={(e) => setAttandance(e.target.value)}
                />
                <input
                  type='number'
                  placeholder='dueAmount'
                  value={dueAmount}
                  onChange={(e) => setDueAmount(e.target.value)}
                />
                <input type='submit' />
                {track ? (
                  <img
                    src={laoding}
                    alt='Loading'
                    style={{ width: "65px", height: "65px" }}
                  />
                ) : (
                  ""
                )}
                <p className='Login'>
                  To Delete Employee
                  <a
                    className='btn'
                    onClick={(e) => {
                      e.preventDefault();
                      toggleForm();
                      setTrack(0);
                      setEmpadd("");
                    }}>
                    Click here
                  </a>
                </p>
              </form>
            </div>
            <div className='imgBx' style={{ background: "#f5f5f5" }}>
              <div className='addemp'>
                <h2>Added Employee</h2>
              </div>
              {empadd ? (
                <div className='data'>
                  <hr />

                  <p>Name: {empadd.name}</p>
                  <p>Salary: {empadd.salary}</p>
                  <p>Advance:{empadd.advanceAmount}</p>
                  <p>Due Amount:{empadd.dueAmount}</p>
                  <p>Attendance :{empadd.attandance}</p>
                  <p>EPF:{empadd.epf}</p>
                  <p>ESI:{empadd.esi}</p>
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
      </section>
    </React.Fragment>
  );
};

export default AddEmployee;
