import React, { Fragment } from "react";
import { useHistory } from "react-router-dom";
import { UserContext } from "../../App";
import img3 from "../../assets/images/img3.jpg";
import laoding from "../../assets/images/loading.svg";
import "../../assets/css/login.css";
import { toast, Zoom } from "react-toastify";
import cookie from "js-cookie";
import { useTranslation } from "react-i18next";
toast.configure();
const Login = () => {
  const { t } = useTranslation();
  const toggleForm = () => {
    let Conatainer = document.querySelector(".Container");
    Conatainer.classList.toggle("active");
  };
  React.useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, []);
  const url = "https://dev-steel-solution.herokuapp.com/api";
  const { dispatch } = React.useContext(UserContext);
  const history = useHistory();
  const [id, setId] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [employeeData, setEmployeeData] = React.useState("");
  const [track, setTrack] = React.useState(0);
  const handleSearch = () => {
    if (!id) {
      setEmployeeData("");
      toast.error("Field Cannot be Empty", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        transition: Zoom,
      });
    } else {
      setTrack(1);
      let myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      let requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };

      fetch(url + `/individual-employee/${id}`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          console.log(result);
          if (!result) {
            setEmployeeData(0);
            setTrack(0);
            toast.error("Please Enter Correct Id", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: true,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              transition: Zoom,
            });
          } else {
            setTrack(0);
            setId("");
            setEmployeeData(result);
          }
        })
        .catch((error) => {});
    }
  };
  const handleSubmit = () => {
    if (!email || !password) {
      toast.error("Field Cannot be Empty", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        transition: Zoom,
      });
    } else {
      setTrack(1);
      let myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      let raw = JSON.stringify({
        email: email,
        password: password,
      });

      let requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw,
        redirect: "follow",
      };

      fetch(url + "/user/login", requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (result.message === "Auth Failed") {
            setTrack(0);
            toast.error("Email And Password Incorrect", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: true,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              transition: Zoom,
            });
          } else {
            if (result.message === "Auth successful") {
              setTrack(0);
              localStorage.setItem("id", result._id);
              cookie.set("jwt-token", result.token, { expires: 1 });
              dispatch({ type: "USER", payload: result.token });
              toast.info("Login Success", {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                transition: Zoom,
              });
              history.push("/employeeDetails");
            } else {
              setTrack(0);
              toast.error("something went Wrong Try again", {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                transition: Zoom,
              });
            }
          }
        })
        .catch((error) => {});
    }
  };
  return (
    <Fragment>
      <section className='loginForm'>
        <div className='Container'>
          <div className='user signinBx'>
            <div className='imgBx'>
              <img src={img3} alt='' />
            </div>
            <div className='formBx getemp'>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  handleSearch();
                }}>
                <h2>{t("login.1")}</h2>
                <input
                  type='text'
                  placeholder='Enter Employee ID'
                  value={id}
                  onChange={(e) => setId(e.target.value)}
                />
                <input type='submit' />
                <p className='Login'>
                  {t("login.4")}
                  <a
                    className='btn'
                    onClick={(e) => {
                      e.preventDefault();
                      setTrack(0);
                      setEmployeeData("");
                      toggleForm();
                    }}>
                    {t("login.5")}
                  </a>
                </p>
                {track ? (
                  <img
                    src={laoding}
                    alt='Loading'
                    style={{ width: "65px", height: "65px" }}
                  />
                ) : (
                  ""
                )}
                {employeeData ? (
                  <div className='data'>
                    <hr />
                    <h4>Employee Data</h4>
                    <p>Name :{employeeData.name}</p>
                    <p>Name :{employeeData.epf}</p>
                    <p>Esi :{employeeData.esi}</p>
                    <p>Advance Amount:{employeeData.salary.advanceAmount}</p>
                    <p>Due Amount :{employeeData.dueAmount}</p>
                    <p>Attendance :{employeeData.attandance}</p>
                  </div>
                ) : (
                  ""
                )}
              </form>
            </div>
          </div>
          <div className='user signupBx'>
            <div className='formBx log'>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  handleSubmit();
                }}>
                <h2>{t("login.2")}</h2>
                <input
                  type='email'
                  placeholder='Enter Email'
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <input
                  type='password'
                  placeholder='Enter Password'
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <input type='submit' />
                {track ? (
                  <img
                    src={laoding}
                    alt='Loading'
                    style={{ width: "70px", height: "70px" }}
                  />
                ) : (
                  ""
                )}
                <p className='Login'>
                  {t("login.3")}
                  <a
                    className='btn'
                    onClick={(e) => {
                      e.preventDefault();
                      setTrack(0);
                      toggleForm();
                    }}>
                    {t("login.5")}
                  </a>
                </p>
              </form>
            </div>
            <div className='imgBx'>
              <img src={img3} alt='' />
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default Login;
