import React, { useEffect, useState } from "react";
import MUIDataTable from "mui-datatables";
import cookie from "js-cookie";
import laoding from "../../assets/images/loading.svg";

const Table = () => {
  const [pass, setPass] = useState("");
  const [track, setTrack] = useState(0);
  const url = "https://dev-steel-solution.herokuapp.com/api";
  useEffect(() => {
    getData();
    // Get the modal
    let modal = document.getElementById("myModal");
    // Get the button that opens the modal
    let btn = document.getElementById("myBtn");
    // Get the <span> element that closes the modal
    let span = document.getElementsByClassName("close")[0];
    // When the user clicks the button, open the modal
    btn.onclick = function () {
      modal.style.display = "block";
    };
    // When the user clicks on <span> (x), close the modal
    span.onclick = function () {
      modal.style.display = "none";
    };
    // When the user clicks anywhere outside of the modal, close it
    window.onclick = function (event) {
      if (event.target === modal) {
        modal.style.display = "none";
      }
    };
  }, []);
  function getData() {
    let myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + cookie.get("jwt-token"));

    let requestOptions = {
      method: "GET",
      headers: myHeaders,
      redirect: "follow",
    };

    fetch(url + "/get-employee", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setData(result);
      })
      .catch((error) => {});
  }
  const updatePass = () => {
    if (!pass) {
      console.log("pass not be empty");
    } else {
      setTrack(1);
      let myHeaders = new Headers();
      myHeaders.append("Authorization", "Bearer " + cookie.get("jwt-token"));
      myHeaders.append("Content-Type", "application/json");

      let raw = JSON.stringify({
        password: pass,
      });

      let requestOptions = {
        method: "PATCH",
        headers: myHeaders,
        body: raw,
        redirect: "follow",
      };
      fetch(url + `/user/${localStorage.getItem("id")}`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (result.message) {
            setTrack(0);
            document.getElementById("trigger").innerHTML = "Password Changed";
            setPass("");
          } else {
            setTrack(0);
            document.getElementById("trigger").innerHTML = "Something Went Wrong";
            setPass("");
          }
        })
        .catch((error) => {});
    }
  };
  const columns = [
    {
      name: "employeeID",
      label: "Employee ID",
    },
    {
      name: "name",
      label: "Employee Name",
    },
    {
      name: "epf",
      label: "EPF",
    },
    {
      name: "esi",
      label: "ESI",
    },
    {
      name: "salary",
      label: "Salary",
    },
    {
      name: "advanceAmount",
      label: "Advance Amount",
    },
    {
      name: "dueAmount",
      label: "Due Amount",
    },
    {
      name: "attandance",
      label: "Attandance",
    },
  ];

  const [data, setData] = useState([]);

  const options = {
    viewColumns: false,
    sort: true,
    filter: true,

    rowsPerPage: 5,
    rowsPerPageOptions: [5, 10, 20, 40, 100],
    customToolbarSelect: () => {},
  };

  return (
    <React.Fragment>
      <section className='editpassword'>
        {/* <!-- our client end --> */}
        <div id='myModal' className='modal'>
          <div className='modal-content'>
            <span className='close'>&times;</span>
            <div className='container'>
              <div className='editpassform'>
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    updatePass();
                  }}>
                  <input
                    type='text'
                    value={pass}
                    placeholder='New Password'
                    onChange={(e) => setPass(e.target.value)}
                  />
                  <input type='submit' />
                  {track ? (
                    <img
                      src={laoding}
                      alt='Loading'
                      style={{ width: "65px", height: "65px" }}
                    />
                  ) : (
                    ""
                  )}
                </form>
                <span id='trigger'></span>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='employeeTable' style={{ padding: "70px 0px 20px 0px" }}>
        <div className='container'>
          <div className='row'>
            <div className='editpassbtn'>
              <div className='submit-btn full-width center'>
                <button type='submit' id='myBtn' className='btn'>
                  Edit Password
                </button>
              </div>
            </div>
            <div className='col-md-12 col-sm-12 tablepadding'>
              <MUIDataTable
                title={"Employee List"}
                data={data}
                columns={columns}
                options={options}
              />
            </div>
          </div>
        </div>
      </section>
    </React.Fragment>
  );
};
export default Table;
