import React from "react";
import cookie from "js-cookie";
import img3 from "../../assets/images/img3.jpg";
import "../../assets/css/login.css";
import laoding from "../../assets/images/loading.svg";
import { toast, Zoom } from "react-toastify";
const UpdateEmployee = () => {
  const url = "https://dev-steel-solution.herokuapp.com/api";
  const [name, setName] = React.useState("");
  const [epf, setEpf] = React.useState("");
  const [esi, setEsi] = React.useState("");
  const [salary, setSalary] = React.useState("");
  const [advanceAmount, setAdvanceAmount] = React.useState("");
  const [dueAmount, setDueAmount] = React.useState("");
  const [attandance, setAttandance] = React.useState("");
  const [id, setId] = React.useState("");

  const [uid, setUid] = React.useState("");
  const [track, setTrack] = React.useState(0);
  const [employeeData, setEmployeeData] = React.useState("");

  const toggleForm = () => {
    let Conatainer = document.querySelector(".Container");
    Conatainer.classList.toggle("active");
  };
  React.useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, []);

  const handleSearch = () => {
    if (!uid) {
      toast.error("Field Cannot be Empty", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        transition: Zoom,
      });
    } else {
      setTrack(1);
      let myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      let requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };
      fetch(url + `/individual-employee/${uid}`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (!result) {
            setTrack(0);
            toast.error("Incorrect Employee Id", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: true,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              transition: Zoom,
            });
          } else {
            setEmployeeData(result);
            setTrack(0);
            setAdvanceAmount(result.advanceAmount);
            setAttandance(result.attandance);
            setDueAmount(result.dueAmount);
            setEpf(result.epf);
            setEsi(result.esi);
            setId(result.employeeID);
            setName(result.name);
            setSalary(result.salary);
          }
        })
        .catch((error) => {});
    }
  };
  const handleUP = () => {
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    let requestOptions = {
      method: "GET",
      headers: myHeaders,
      redirect: "follow",
    };
    fetch(url + `/individual-employee/${uid}`, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        if (!result) {
          toast.error("Incorrect Employee Id", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            transition: Zoom,
          });
          setTrack(0);
        } else {
          setEmployeeData(result);
          setTrack(0);
        }
      })
      .catch((error) => {});
  };
  const handleUpdate = () => {
    if (!id) {
      toast.error("Field cannot be empty", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        transition: Zoom,
      });
    } else {
      setTrack(1);
      let myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      let requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };
      fetch(url + `/individual-employee/${id}`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (!result) {
            toast.error("Incorrect Employee Id", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: true,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              transition: Zoom,
            });
          } else {
            var myHeaders = new Headers();
            myHeaders.append("Authorization", "Bearer " + cookie.get("jwt-token"));
            myHeaders.append("Content-Type", "application/json");

            var raw = JSON.stringify({
              name: name,
              epf: epf,
              esi: esi,
              salary: salary,
              advanceAmount: advanceAmount,
              attandance: attandance,
              dueAmount: dueAmount,
            });

            let requestOptions = {
              method: "PATCH",
              headers: myHeaders,
              body: raw,
              redirect: "follow",
            };

            fetch(url + `/${result._id}`, requestOptions)
              .then((response) => response.json())
              .then((result) => {
                if (result.message == "Employee data updated successfully") {
                  setTrack(0);

                  toast.info("Employee updated", {
                    position: "top-right",
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    transition: Zoom,
                  });

                  setAdvanceAmount("");
                  setAttandance("");
                  setDueAmount("");
                  setEpf("");
                  setEsi("");
                  setId("");
                  setName("");
                  setSalary("");
                  handleUP();
                } else {
                  toast.error("Something went wrong", {
                    position: "top-right",
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    transition: Zoom,
                  });
                  setTrack(0);
                }
              })
              .catch((error) => {});
          }
        })
        .catch((error) => {});
    }
  };
  return (
    <React.Fragment>
      <section className='loginForm'>
        <div className='Container upfrom'>
          <div className='user signinBx '>
            <div className='imgBx'>
              <img src={img3} alt='' />
            </div>
            <div className='formBx ugetemp'>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  handleSearch();
                }}>
                <h2>Get Employee Details</h2>
                <input
                  type='text'
                  placeholder='Enter Employee ID'
                  value={uid}
                  onChange={(e) => setUid(e.target.value)}
                />
                <input type='submit' />
                <p className='Login'>
                  To Update Employee
                  <a
                    className='btn'
                    onClick={(e) => {
                      e.preventDefault();
                      toggleForm();
                    }}>
                    Click here
                  </a>
                </p>
                {track ? (
                  <img
                    src={laoding}
                    alt='Loading'
                    style={{ width: "65px", height: "65px" }}
                  />
                ) : (
                  ""
                )}
                {employeeData ? (
                  <div className='data'>
                    <hr />
                    <h4>Employee Data</h4>
                    <p>Name: {employeeData.name}</p>
                    <p>Salary: {employeeData.salary}</p>
                    <p>Advance:{employeeData.advanceAmount}</p>
                    <p>Due Amount:{employeeData.dueAmount}</p>
                    <p>Attendance :{employeeData.attandance}</p>
                    <p>EPF:{employeeData.epf}</p>
                    <p>ESI:{employeeData.esi}</p>
                  </div>
                ) : (
                  ""
                )}
              </form>
            </div>
          </div>
          <div className='user signupBx'>
            <div className='formBx ufrom'>
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  handleUpdate();
                }}>
                <label>Id:</label>
                <input
                  type='text'
                  placeholder='Employee Id'
                  value={id}
                  onChange={(e) => setId(e.target.value)}
                />
                <label>Name :</label>
                <input
                  type='text'
                  placeholder='name'
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
                <label>EPF :</label>
                <input
                  type='text'
                  placeholder='epf'
                  value={epf}
                  onChange={(e) => setEpf(e.target.value)}
                />
                <label>ESI :</label>
                <input
                  type='text'
                  placeholder='esi'
                  value={esi}
                  onChange={(e) => setEsi(e.target.value)}
                />
                <label>Salary :</label>
                <input
                  type='number'
                  placeholder='salary'
                  value={salary}
                  onChange={(e) => setSalary(e.target.value)}
                />
                <label>Advance Amt :</label>
                <input
                  type='number'
                  placeholder='advanceAmount'
                  value={advanceAmount}
                  onChange={(e) => setAdvanceAmount(e.target.value)}
                />
                <label>Attandance :</label>
                <input
                  type='number'
                  placeholder='attendance'
                  value={attandance}
                  onChange={(e) => setAttandance(e.target.value)}
                />
                <label>Due Amt :</label>
                <input
                  type='number'
                  placeholder='dueAmount'
                  value={dueAmount}
                  onChange={(e) => setDueAmount(e.target.value)}
                />
                <input type='submit' />
                {track ? (
                  <img
                    src={laoding}
                    alt='Loading'
                    style={{ width: "65px", height: "65px" }}
                  />
                ) : (
                  ""
                )}
                <p className='Login'>
                  To get Employee Details
                  <a
                    className='btn'
                    onClick={(e) => {
                      e.preventDefault();
                      toggleForm();
                    }}>
                    Click here
                  </a>
                </p>
              </form>
            </div>
            <div className='imgBx udata' style={{ background: "#f5f5f5" }}>
              <h2>Update Employee</h2>

              {employeeData ? (
                <div className='data'>
                  <hr />
                  <h4>Employee Data</h4>
                  <p>Name: {employeeData.name}</p>
                  <p>Salary: {employeeData.salary}</p>
                  <p>Advance:{employeeData.advanceAmount}</p>
                  <p>Due Amount:{employeeData.dueAmount}</p>
                  <p>Attendance :{employeeData.attandance}</p>
                  <p>EPF:{employeeData.epf}</p>
                  <p>ESI:{employeeData.esi}</p>
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
      </section>
    </React.Fragment>
  );
};

export default UpdateEmployee;
